:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(225deg,
            var(--toggle-bg-end) 0%,
            var(--toggle-bg-start) 98.02%);
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(225deg,
          var(--toggle-bg-end) 0%,
          var(--toggle-bg-start) 98.02%);
  --toggle-button: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a, a:visited, a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  background: var(--card-bg);
  padding: 1.5625rem;
  text-align: center;
  border-radius: 0.3125rem;
  flex: 1;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 4px solid var(--facebook);
}
.card--twitter {
  border-top: 4px solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle), var(--instagram-start));
  position: absolute;
  display: block;
  top: 0;
  left: 0;
}
.card--youtube {
  border-top: 4px solid var(--youtube);
}
.card_subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card_platform {
  height: 1.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
}
.card_icon {
  margin-right: 0.5rem;
}
.card_username {
  font-size: 0.75rem;
  font-weight: bold;
}
.card_followers {
  margin-bottom: 1.5625rem;
}
.card_count {
  color: var(--text-color);
  font-weight: bold;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.5625rem;
}
.card_count--big {
  font-size: 3.5rem;
}
.card_count--small {
  font-size: 2rem;
}
.card_label {
  font-size: 0.75rem;
  font-weight: 400;
  color: var(--text-color2);
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card_change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: bold;
}
.card_change--up {
  color: var(--limegreen);
}
.card_change--down {
  color: var(--brightred);
}
.card_change img {
  margin-right: 0.25rem;
}
.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--toggle);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle_wrapper {
  height: 1.5rem;
  grid-column: 2/3;
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -2px 0 -2px;
  width: 1.5rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle_button {
  border: 2px solid white;
}
.toggle_background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle_button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle_button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle_button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header_subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
}
.header_title {
  width: 100%;
}
@media (min-width: 40em) {
  .header_title {
    width: auto;
  }
}
.header_toggle {
  margin-left: auto;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  gap: 1.5rem;
}
.card-grid .card_count {
  margin-bottom: 0;
}
.card-grid .card_count, .card-grid .card_change {
  align-self: end;
}
.card-grid img, .card-grid .card_change {
  justify-self: end;
}